.advantages-about-wrapper {
  padding-top: 82px;
  padding-bottom: 97px;

  background-image: url("./../img/triangles-background-desktop@1x.png");
  background-repeat: no-repeat;
  background-position: center -29px;
  background-size: auto;

  @media (min-resolution: $retina-dpi) and (min-resolution: $retina-dppx) {
    background-image: url("./../img/triangles-background-desktop@2x.png");
    background-size: 1920px 1347px, cover;
  }
}

@media (max-width: $desktop-width) {
  .advantages-about-wrapper {
    padding-top: 22px;
    padding-bottom: 75px;

    background-image: url("./../img/triangles-background-tablet@1x.png");
    background-position: 100% 88px;

    @media (min-resolution: $retina-dpi) and (min-resolution: $retina-dppx) {
      background-image: url("./../img/triangles-background-tablet@2x.png");
      background-size: 559px 873px, cover;
    }
  }
}

@media (max-width: $tablet-width) {
  .advantages-about-wrapper {
    padding-top: 17px;
    padding-bottom: 65px;

    background-image: url("./../img/triangles-background-mobile@1x.png");
    background-position: 100% 75.5%;

    @media (min-resolution: $retina-dpi) and (min-resolution: $retina-dppx) {
      background-image: url("./../img/triangles-background-mobile@2x.png");
      background-size: 320px 873px, cover;
    }
  }
}
